import React from "react";
import CodeCard from "./CodeCard";
import EventExampleOnC from "../examples/EventExapmleOnC";
import EventExampleOnOffBtn from "../examples/EventExampleOnOffBtn";

export default function Component() {
  let code_1 = `export default function EventExampleOnC() {
  return (
    <div className="example ex_event2">
      <p>open console to get a gift!</p>
      <div className="btns">
        <input
          placeholder='type "cat"'
          onChange={(event) => {
            if (event.target.value === "cat") console.log("you got a cat!");
          }}
        ></input>
        <button onClick={() => console.log("+1 cat")} className="btn">
          Click to get 1 more
        </button>
      </div>
    </div>
  )}`;

  let code_2 = `           function Form() {
                function handleSubmit(e) {
                  e.preventDefault();
                  console.log(' .');
                }
                return (
                  <form onSubmit={handleSubmit}>
                    <button type="submit"></button>
                  </form>
                );
            }`;

  let code_3 = ` function Toggle() {
      let [isToggleOn, setIsToggleOn] = useState(true);
      function handleClick() {
        setIsToggleOn(!isToggleOn)
      }
  return (
      <button className={'toggle__btn'} onClick={() => handleClick()}>
        {isToggleOn ? 'On' : 'Off'}
     </button>
        )}`;

  return (
    <div className="content">
      <div className="content__text">
        <p>
           JSX        .
            React    camelCase   .
        </p>
        <p>  : onClick, onChange</p>
      </div>

      <div className="content__row">
        <div className="content__code">
      <CodeCard codeString={code_1}/>
        </div>
        <div className="content__code">
<EventExampleOnC/>
        </div>
      </div>

      <div className="content__text">
        <p>   ,   :</p>
        <ol>
          <li> : onCopy, onCut, onPaste</li>
          <li>
            / : onCompositionEnd, onCompositionStart,
            onCompositionUpdate
          </li>
          <li>: onKeyDown, onKeyPress, onKeyUp</li>
          <li>: onFocus, onBlur</li>
          <li>: onChange, onInput, onSubmit</li>
          <li>
            : onClick, onContextMenu, onDoubleClick, onDrag, onDragEnd,
            onDragEnter, onDragExit, onDragLeave, onDragOver, onDragStart,
            onDrop, onMouseDown, onMouseEnter, onMouseLeave, onMouseMove,
            onMouseOut, onMouseOver, onMouseUp
          </li>
          <li>: onSelect</li>
          <li>: onTouchCancel, onTouchEnd, onTouchMove, onTouchStart</li>
          <li>UI: onScroll</li>
          <li> : onWheel</li>
          <li>
            : onAbort, onCanPlay, onCanPlayThrough, onDurationChange,
            onEmptied, onEncrypted, onEnded, onError, onLoadedData,
            onLoadedMetadata, onLoadStart, onPause, onPlay, onPlaying,
            onProgress, onRateChange, onSeeked, onSeeking, onStalled, onSuspend,
            onTimeUpdate, onVolumeChange, onWaiting
          </li>
          <li>: onLoad, onError</li>
          <li>
            : onAnimationStart, onAnimationEnd, onAnimationIteration
          </li>
          <li>: onTransitionEnd</li>
        </ol>
      </div>

      <div className="content__text">
        <p>
           React      , 
          false.    preventDefault.
        </p>
      </div>

      <div className="content__code">
        <CodeCard codeString={code_2} />
      </div>

      <div className="content__text">
        <p>
           ,    ES6-,  
                 . ,
            Toggle  ,   
               :
        </p>
      </div>

      <div className="content__row">
        <div className="content__code">
          <CodeCard codeString={code_3} />
        </div>
        <div className="content__code">
          <EventExampleOnOffBtn />
        </div>
      </div>
    </div>
  );
}


