import React from "react";
import CodeCard from "./CodeCard";
import RefsExample from "../examples/RefsExample";

export default function Component() {
  let code_1 = `import React, {useEffect, useState, Fragment, useRef} from 'react'

const Test = () => {
 const [text, setText] = useState('')
  const textInput = useRef(null)
  const valid = (e) => {
    textInput.current.focus()
  }
    return (
      <div>
          <input ref={textInput} />
          <button onClick={(event) => valid()} value={text}>
            Click to focus
          </button>
      </div>
    )}

export default Test`;

  return (
    <div className="content">
      <div className="content__text">
        <p>
          Refs (  )     DOM-
           React-,    render().
        </p>
        <p>     :</p>
        <ol>
          <li>
             ,    
            .
          </li>
          <li>    .</li>
          <li>
               ,   DOM.
          </li>
        </ol>
        <p>  -   :</p>
      </div>

      <div className="content__row">
        <div className="content__code">
          <CodeCard codeString={code_1} />
        </div>
        <div className="content__code">
          <RefsExample />
        </div>
      </div>
    </div>
  );
}
